// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index.js'
import {Icon,Tabbar,TabbarItem,Swipe,SwipeItem,Lazyload,Search,GoodsAction, GoodsActionIcon, GoodsActionButton,Card,Button,SubmitBar,Popup,Toast} from 'vant'
Vue.use(Icon).use(Tabbar).use(TabbarItem).use(Swipe).use(SwipeItem).use(Lazyload).use(Search).use(GoodsAction).use(GoodsActionIcon).use(GoodsActionButton).use(Card).use(Button).use(SubmitBar).use(Popup).use(Toast)

Vue.config.productionTip = false
Vue.filter("toFixed",(target,val=2)=>{
  return Number(target).toFixed(val)
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})

let w = document.documentElement.clientWidth || document.body.clientWidth
document.documentElement.style.fontSize = w/640 * 100 + "px"


