import * as types from './mutationType.js';
export default {
    [types.ADD_GOODS](state,payload){
        let id = payload.id
        let index = state.goods.findIndex(item => parseInt(item.id) === parseInt(id))
        let count = 1
        if(index > -1){
            state.goods[index].count += 1
        }else{
            state.goods.push({...payload,count})
        }
    },
    [types.MINUS_ONE](state,id){
        let index = state.goods.findIndex(item => parseInt(item.id) === parseInt(id))
        if(index > -1){
            state.goods[index].count -= 1
        }
        if(state.goods[index].count === 0){
            state.goods.splice(index,1)
        }
    },
    [types.ADD_ONE](state,id){
        let index = state.goods.findIndex(item => parseInt(item.id) === parseInt(id))
        if(index > -1){
            state.goods[index].count += 1
        }
    }
}


