import * as types from './mutationType.js';
export default {
    add({commit},id){
        commit(types.ADD_GOODS,id)
    },
    minusOne({commit},id){
        commit(types.MINUS_ONE,id)
    },
    addOne({commit},id){
        commit(types.ADD_ONE,id)
    }
}


