<template>
    <div class="container">
        <div class="base-info">
            <div class="user-look">
                <img :src="imgHead" alt="">
            </div>
            <div>
                <p>zhangzongzhi</p>
                <p>to be or not to be,is a question</p>
            </div>
            <van-icon name="setting" class="ico-set"></van-icon>
        </div>
        <tabBar :active="3"></tabBar>
    </div>
</template>
<script>
    import tabBar from "../base/tabBar.vue"
    import imgHead from "../../assets/images/headImg1.jpg"
    export default{
        name:"Personal",
        data(){
            return{
                imgHead
            }
        },
        components:{tabBar,imgHead}
    }
</script>
<style scoped>
    .container{
        background-color:#dddddd38;
    }
    img{
        width:100px;
        height:150px;
    }
    .base-info{
        background-color: #ffe384;
        padding:0.5rem 1rem;
        display: flex;
        flex-direction: row;
        flex-wrap:nowrap;
    }
    .ico-set{
        position:absolute;
        right:0.2rem;
        top:0.4rem;
        color:#fff
    }
    .user-look{
        width:1.5rem;
        height: 1.5rem;
        margin-right: 0.3rem;
    }
    .user-look img{
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }
</style>


