import Vue from 'vue'
import Vuex from 'vuex'
import actions from './action.js'
import mutations from './mutation.js'
import getters from './getters.js'
Vue.use(Vuex)
export default new Vuex.Store({
    state:{
      goods:[]
    },
    mutations,
    actions,
    getters
})



