<template>
    <div class="container">
        <div v-if="!getTotal">请购物</div>
        <div class="goods-info" v-for="item in goods">
            <van-card
            :num="item.count"
            :price="item.nPrice * item.count"
            :desc="item.message"
            :title="item.name"
            :thumb="item.url"
            >
            <template #footer>
                <van-button size="mini" type="default" @click="minusOne(item.id)">-</van-button>
                <van-button size="mini" type="default" @click="addOne(item.id)">+</van-button>
            </template>
        </van-card>
        <!-- <van-submit-bar class="submit-bar" :price="3050" button-text="提交订单" @submit="onSubmit" /> -->
        </div>
        <tabBar :active="2"></tabBar>
    </div>
</template>
<script>
    import tabBar from "../base/tabBar.vue"
    import {mapState,mapActions,mapGetters} from 'vuex'
    export default{
        name:"Car",
        data(){
            return{
                price:"3050",
            }
        },
        computed:{
            ...mapGetters([
                'getTotal',
                'goods'
            ])
        },
        methods:{
            onSubmit(){
                console.log('submit')
            },
            ...mapActions([
                'minusOne',
                'addOne'
            ])
        },
        components:{tabBar}
    }
</script>
<style scoped>
    .goods-indo{
        /* display: flex; */
        position: relative;
        /* flex-direction: column;
        justify-content: center; */
        top: -5px;
    }   
</style>


