<template>
    <div>
        <div class="home-search">
            <form action="/">
                <van-search
                  v-model="value"
                  show-action
                  placeholder="请输入搜索关键词"
                  @search="onSearch"
                  @cancel="onCancel"
                />
              </form>
        </div>
        <div class="home-swipe">
            <slides :slides="slides"></slides>
        </div>
        <div class="recommend">
            <h3>推荐</h3>
            <swiper :options="swiperOption">
                <swiper-slide v-for="(slide,index) in goods" :key="index">
                    <div class="slide-item" @click="$router.push({params:{goodsId:slide.goodsId},name:'detail'})">
                        <img :src="slide.image" alt="" width="80%" >
                        <p>{{slide.goodsName}}</p>
                        <p>￥{{slide.nPrice|toFixed}}({{slide.price|toFixed}})</p>
                    </div>
                </swiper-slide>
            </swiper>
        </div>
        <tabBar :active="0"></tabBar>
    </div>
</template>
<script>
    import tabBar from "../base/tabBar.vue"
    // import {getSlideData} from "../../api"
    import slides from "../base/slides.vue"
    import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper'
    import "swiper/css/swiper.css"
    export default{
        name:"Home",
        data(){
            return {
                swiperOption:{slidesPerView:3},
                slides:[
                        {image:"https://img.yzcdn.cn/vant/apple-1.jpg","goodsId":"1234567"},
                        {image:"https://img.yzcdn.cn/vant/apple-2.jpg","goodsId":"2345678"},
                        {image:"https://img.yzcdn.cn/vant/apple-3.jpg","goodsId":"3456789"}
                    ],
                value:"开心",
                goods:[
                        {goodsId:123,"goodsName":"牛肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999},
                        {goodsId:456,"goodsName":"猪肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999},
                        {goodsId:678,"goodsName":"羊肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999},
                        {goodsId:789,"goodsName":"鸡肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999},
                        {goodsId:121,"goodsName":"鸭肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999},
                        {goodsId:242,"goodsName":"鹅肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999},
                    ]             
            }
        },
        methods:{
            onSearch(){},
            onCancel(){}
        },
        components:{
            tabBar,
            slides,
            Swiper,
            SwiperSlide
        },
        directives:{
            swiper:directive
        }
    }
</script>
<style scoped>
    .recommend{
        background-color: #fff;
    }
    .recommend .slide-item{
        box-sizing: border-box;
        padding:0.1rem;
        text-align: center;
        font-size: 12px;
        border-right: 1px solid #ddd;
    }
    .recommend .slide-item p{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
</style>


