<template>
    <div>
        <div class="goods-img">
            <img :src="url" alt="">
            <p>商品:{{name}}</p>
            <p>￥{{nPrice}}(<del>{{price}}</del>)</p>
            <p>功能:{{message}}</p>
        </div>
        <div class="goods-action">
            <van-goods-action>
                <van-goods-action-icon icon="chat-o" text="客服" />
                <van-goods-action-icon icon="shop-o" text="店铺" />
                <van-goods-action-icon icon="star-o" text="收藏" />
                <van-goods-action-button
                  type="warning"
                  text="加入购物车"
                  @click="()=>{add({id,name,url,nPrice,message});$toast.success('添加成功');}"
                />
                <van-goods-action-button
                  type="danger"
                  text="立即购买"
                  @click="onClickButton"
                />
              </van-goods-action>
        </div>
        <van-popup v-model="show" class="show">冷静</van-popup>
    </div>
</template>
<script>
    import { Toast } from 'vant';
    import {mapState,mapActions} from 'vuex'
    // import {getSlideData} from "../../api" 
    export default{
        name:"Detail",
        data(){
            return{
                show:false,
                id:'123',
                url:'',
                nPrice:'',
                price:'',
                message:'',
                name:'',
                goods:[
                    {goodsId:123,"goodsName":"牛肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999,"message":"1234weibizhi"},
                    {goodsId:456,"goodsName":"猪肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999,"message":"1234weibizhi"},
                    {goodsId:678,"goodsName":"羊肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999,"message":"1234weibizhi"},
                    {goodsId:789,"goodsName":"鸡肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999,"message":"1234weibizhi"},
                    {goodsId:121,"goodsName":"鸭肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999,"message":"1234weibizhi"},
                    {goodsId:242,"goodsName":"鹅肉",image:"https://img.yzcdn.cn/vant/apple-4.jpg","nPrice":178,"price":999,"message":"1234weibizhi"},
                ]
            }
        },
        async created(){
            // let data = await getSlideData()
            // this.goods = data.goods
            let item = this.goods.find(item => Number(item.goodsId) === Number(this.id))
            let {goodsName,image,nPrice,price,message} = item
            this.url = image
            this.name = goodsName
            this.nPrice = nPrice
            this.price = price
            this.message = message
        },
        watch:{
            $route:{
                handler(){
                    this.id = this.$route.params.goodsId
                },
                immediate:true
            }
        },
        methods:{
            onClickButton(){
                this.show = true;
            },
            ...mapActions([
                'add'
            ])
        }
    }
</script>
<style scoped>
    img{
        width: 375px;
        height: 375px;
    }
    .show{
        width: 50%;
        height: 50%;
        background-color: rgba(255, 255, 255, 0);
        color:white;
        font-size: 100px;
        text-align: center;
    }
</style>


