<template>
    <van-tabbar v-model="index">
        <van-tabbar-item icon="home-o" to="/home">首页</van-tabbar-item>
        <van-tabbar-item icon="wap-nav" to="/list">分类</van-tabbar-item>
        <van-tabbar-item icon="cart" :info="getTotal" to="/car">购物车</van-tabbar-item>
        <van-tabbar-item icon="contact" info="20" to="/personal">我的</van-tabbar-item>
    </van-tabbar>
</template>
<script>
    import {mapState,mapActions,mapGetters} from 'vuex'
    export default{
        computed:{
            index:{
                get(){
                    return this.active
                },
                set(val){

                }
            },
            ...mapGetters([
                'getTotal'
            ])
            
        },
        props:["active"]
    }
</script>
<style>
    
</style>


